<template>
  <div class="wrapper">
    <typeChange @change="changeType"></typeChange>
    <div class="content">
      <div class="c-left">
        <!--<div class='c-l-top'>-->
          <!--<ctitle :title="'工作过程监控'"></ctitle>-->
          <!--<div class='c-top-content'>-->
            <!--<gcgcjkDiv :data="gzgcjkData"></gcgcjkDiv>-->
          <!--</div>-->
        <!--</div>-->
        <div class='c-l-bottom'>
          <div class='charts'>
            <p>总体</p>
            <div class='all'>
              <blfsList :data="ztListData" :key='keyValue0' @openListClick="openList0"></blfsList>
            </div>
          </div>
          <div class='charts'>
            <p>信访部门</p>
            <div class='all'>
              <blfsList :data="xfbmListData" :key='keyValue1' @openListClick="openList1"></blfsList>
            </div>
          </div>
          <div class='charts'>
            <p>责任单位</p>
            <div class='all'>
              <blfsList :data="zrdwListData" :key='keyValue2' @openListClick="openList2"></blfsList>
            </div>
          </div>
        </div>
      </div>
      <div class="c-right">
        <ctitle :title="`${curType}区划分布`"></ctitle>
        <div class="part">
          <div class="charts">
            <div v-if="mapParams.options.JSON!==cookieCity" class="map-back" @click="mapBack"></div>
            <transition name="fade">
              <Map v-if="mapFlag" :params="mapParams" @change="handleMap"></Map>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Cookies from 'js-cookie'
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import typeChange from "@/components/typeChange/index";
  import Map from "@/components/map/Map";
  import gcgcjkDiv from "@/components/gcgcjk/index";
  import qhfbbox from "@/components/zdqh/index";
  import blfsList from "@/components/list/allList";

  export default {
    name: "qhfb",
    components: {
      ctitle,
      typeChange,
      Map,
      gcgcjkDiv,
      qhfbbox,
      blfsList
    },
    data() {
      return {
        curTypeId: 'drop',
        curType:"总体-按期答复率",
        curCity: "广东省",
        keyValue0: false,
        keyValue1: false,
        keyValue2: false,
        shijCity: "",
        shej_02: "invalid",
        shij_02: "invalid",
        xj_02: "invalid",
        nameP: "",
        mapJson: {
          "广东省": "440000",
          "广州市": "440100",
          "韶关市": "440200",
          "深圳市": "440300",
          "珠海市": "440400",
          "汕头市": "440500",
          "佛山市": "440600",
          "江门市": "440700",
          "湛江市": "440800",
          "茂名市": "440900",
          "肇庆市": "441200",
          "惠州市": "441300",
          "梅州市": "441400",
          "汕尾市": "441500",
          "河源市": "441600",
          "阳江市": "441700",
          "清远市": "441800",
          "东莞市": "441900",
          "中山市": "442000",
          "潮州市": "445100",
          "揭阳市": "445200",
          "云浮市": "445300",
        },
        // 1工作过程监控
        gzgcjkData: [],
        // 2总体
        ztListData: [],
        // 3信访部门
        xfbmListData: [],
        // 4责任单位
        zrdwListData: [],
        // 5地图
        mapFlag: false,
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "100%",
          },
          data: [],
          options: {
            JSON: "440000",
            xName: "name",
            yName: "value",
            map: {
              "value": "数量"
            },
            carousel: true
          }
        },
      };
    },
    mounted() {
      this.init(this.date);
    },
    methods: {
      init(date) {
        for (let key in this.mapJson) {
          if (this.mapJson[key] === this.cookieCity) {
            this.curCity = key
          }
        }
        this.mapParams.options.JSON = this.cookieCity;
        this.getAPI(date);
        this.getMapAPI(date);
      },
      // 头部类型切换
      changeType(id) {
        this.curTypeId = id;
        this.getAPI(this.date);
        this.getMapAPI(this.date);
        // this.openList0(this.name, this.curTypeId)
        // this.openList1(this.name, this.curTypeId)
        // this.openList2(this.name, this.curTypeId)
      },

      openList0(name) {
        this.keyValue1 = !this.keyValue1;
        this.keyValue2 = !this.keyValue2;
        this.curType = "总体-"+name;
        this.getMapAPI(this.date)
      },
      openList1(name) {
        this.keyValue0 = !this.keyValue0;
        this.keyValue2 = !this.keyValue2;
        this.curType = "信访部门-"+name;
        this.getMapAPI(this.date)
      },
      openList2(name) {
        this.keyValue0 = !this.keyValue0;
        this.keyValue1 = !this.keyValue1;
        this.curType = "责任单位-"+name;
        this.getMapAPI(this.date)
      },
      getAPI(date) {
        // 1工作过程监控
        // get_http("xfjxkhqhfb", {
        //   gd_id3: "全省绩效考核",
        //   gd_id4: this.curTypeId,
        //   day: `[${date.startDate},${date.endDate}]`
        // })
        //   .then(res => {
        //     this.gzgcjkData = res.data.data;
        //   })
        //   .catch(err => {
        //     console.log(err);
        //   });
        // 2总体
        get_http("xfjxkhqhfb", {
          gd_id3: "总体",
          gd_id4: this.curTypeId,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.ztListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3信访部门
        get_http("xfjxkhqhfb", {
          gd_id3: "信访部门",
          gd_id4: this.curTypeId,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.xfbmListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4责任单位
        get_http("xfjxkhqhfb", {
          gd_id3: "责任单位",
          gd_id4: this.curTypeId,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.zrdwListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 5地图
      getMapAPI(date) {
        // 处理接口参数
        // 省级
        if (this.curCity === '广东省') {
          this.shej_02 = this.curCity;
          this.shij_02 = 'invalid';
          this.xj_02 = 'invalid';
          this.nameP = 'shij_02';
        } else {
          // 市级
          if (this.shijCity) {
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = 'invalid';
            this.nameP = 'xj_02';
          } else {
            // 县区级
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = this.curCity;
            this.nameP = 'invalid';
          }
        }
        get_http("xfjxkhqhfb", {
          // shej_02: this.shej_02,
          // shij_02: this.shij_02,
          // xj_02: this.xj_02,
          // name: this.nameP,
          day: `[${date.startDate},${date.endDate}]`,
          gd_id3: "区划",
          gd_id4: this.curTypeId,
          gd_id5: this.curType,
        })
          .then(res => {
            console.log(res.data);
            // this.mapParams.options.map = res.data.map;
            this.mapParams.data = res.data.data;
            this.mapFlag = true;
            // this.dataListParams.data = this.mapParams.data
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击地图下钻
      handleMap(name) {
        this.curCity = name;
        for (let key in this.mapJson) {
          if (this.curCity === key) {
            this.mapFlag = false;
            this.mapParams.options.JSON = this.mapJson[name];
          }
        }
      },
      // 地图返回全省
      mapBack() {
        this.mapFlag = false;
        this.curCity = "广东省";
        this.mapParams.options.JSON = "440000";
      }
    },
    computed: {
      cookieCity() {
        return Cookies.get('xzqhdm')
      },
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      curCity: function (newVal, oldVal) {
        this.curReci = "drop";
        if (newVal === '广东省') {
          this.getMapAPI(this.date);
        } else {
          let c = '';
          for (let key in this.mapJson) {
            if (newVal === key) {
              this.shijCity = newVal;
              c = newVal;
              this.getMapAPI(this.date);
            }
          }
          if (!c) {
            this.xj_02 = newVal;
            this.getAPI(this.date)
          }
        }
      },
      date(val) {
        this.getAPI(val);
        this.getMapAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper /deep/ .el-radio__label
    font-size .18rem

  .wrapper /deep/ .el-radio__input.is-checked + .el-radio__label
    color #f996d6
    cursor pointer

  .wrapper /deep/ .el-radio__input.is-checked .el-radio__inner
    border-color #f996d6
    background-color #f996d6

  .wrapper /deep/ .el-radio
    color #cbfbff
    display block
    line-height .35rem

  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .fade-enter-active, .fade-leave-active
      transition opacity .3s
    .fade-enter, .fade-leave-to
      opacity 0
    .w-seach
      position absolute
      top -78px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 262px
      line-height 62px
      font-size 22px
      color #cbfbff
      background url("../../../assets/seach-bg.png") no-repeat
      cursor pointer
      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis

    .content
      width 100%
      height 100%
      display flex
      flex-direction row
      padding .2rem .3rem
      box-sizing border-box

      .c-left
        flex .9
        position relative
        box-sizing border-box
        flex-direction column
        display flex
        .c-l-top
          flex .6
          box-sizing border-box
          display flex
          flex-direction column
          .c-top-content
            display flex
            flex .8
        .c-l-bottom
          flex 1
          display flex
          flex-direction column
          .charts
            flex 1
            display flex
            flex-direction row
            align-items center
            p
              flex .2
              font-size 16px
              color #d0f0fb
              text-align center
              padding-left .6rem
            .all
              flex 1.2
              .blfs-list
                height 106px
      .c-right
        flex 1.1
        position relative
        box-sizing border-box
        flex-direction column
        justify-content center
        display flex
        .part
          padding-bottom .1rem
          flex 4
          display flex
          flex-direction row
          width 100%
          height 100%
          position relative
          .pointQhfb

            position absolute

            bottom .5rem
            right 1.2rem
          .w-radio
            position absolute
            top .2rem
            left .7rem
            z-index 10
          .charts
            position relative
            flex 1
            width 100%
            height 100%

          .fade-enter-active, .fade-leave-active
            transform scale(1)
            transition all 0.8s

          .fade-enter, .fade-leave-to
            opacity 0
            transform scale(0)

          .map-back
            position absolute
            top 30px
            right 60px
            z-index 1000
            width 30px
            height 30px
            background url("~@/assets/back-icon.png") no-repeat
            cursor pointer

</style>
