            <th>{{index+1}}</th>
            <th> {{item.name}} </th>
            <th class='changeColor'> {{item.value_g0_1}}% </th>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index",
    props: {
      params: Object
    },
    data() {
      return {
