        data:[],
        datalistTOP5:[],
      };
    },
    created(){
      this.init()

    },
    methods:{
      init(){
        this.datalistTOP5 = this.params.data.filter((item,index)=>{
          return index<5
        })

        console.log(this.datalistTOP5);
      }
    }
  }
</script>

