<!--办理方式-->
<template>
  <div class="blfs-list"
       v-loading="!data.length"
       :element-loading-background="$STANDARD.loadingBgColor">
    <ul v-if="data.length">
      <li
        class="item"
        v-for="(item,index) of data"
        :key="index"
        :style="{
        left:(data.length%2 ===0?evenPosArr[index].left:oddPosArr[index].left)+'px',
        }"
      >
        <p :class="name == item.name ? 'name active' : 'name'" :style="{lineHeight:item.name.length>2?'20px':'40px'}" @click="openList(item.name)">{{item.name}}</p>
        <p class="value">{{(item.value*100).toFixed(2)}}%</p>
        <p class="unit">占比</p>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "allList",
    props: {
      data: Array,
      // type:{
      //   type:String,
      //   default:'odd'
      // }
    },
    data() {
      return {
        name: '',
        // 此处只有7项数据和8项数据之分，奇数：odd,偶数：even
        oddPosArr: [
          {
            "top": 40,
            "left": 0,
          },
          {
            "top": 40,
            "left": 215,
          },
          {
            "top": 40,
            "left": 435,
          },
          {
            "top": 170,
            "left": 0,
          },
          {
            "top": 170,
            "left": 180,
          },
          {
            "top": 170,
            "left": 360,
          },
          {
            "top": 170,
            "left": 540,
          },
        ],
        evenPosArr: [
          {
            "top": 40,
            "left": 0,
          },
          {
            "top": 40,
            "left": 215,
          },
          {
            "top": 40,
            "left": 360,
          },
          {
            "top": 40,
            "left": 540,
          },
          {
            "top": 170,
            "left": 0,
          },
          {
            "top": 170,
            "left": 180,
          },
          {
            "top": 170,
            "left": 360,
          },
          {
            "top": 170,
            "left": 540,
          },
        ]
      }
    },
    computed: {
      styleType() {
        return this.$store.state.styleType;
      }
    },
    methods:{
      openList(name){
        this.name = name;
        this.$emit("openListClick", name);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .blfs-list
    width 100%
    position relative
    li
      left 205px
    li.item
      position absolute
      width 189px
      height 106px
      background url("./img/blfs-icon2.png") no-repeat
      .name
        position absolute
        top 35px
        left 32px
        width 50px
        font-size 16px
        color #fff
        text-align center
        cursor pointer
      .active
        text-shadow: 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4;

      .value
        position absolute
        top 45px
        right 15px
        font-size 16px
        color #ffde00
        font-weight 700
      .unit
        position absolute
        bottom 22px
        right 25px
        font-size 14px
        color #12dded
    li:first-child
      left 0
</style>
